/* ==================
    general styling
===================== */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--primary-color);
  max-width: 100%;
}

/* ==================
    CSS variables
===================== */

:root {
  --primary-color: #435334;
  --secondary-color: #cedebd;
  --tertiary-color: #ffffff;
  --shrink-factor: 1;
}

/* ==========================
    prevent color changes of the input backgrounds 
============================= */

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}

/* ==================
    Form component
===================== */

.form--layout {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 30%;
  margin-top: 40px;
  margin-left: 3rem;
  margin-bottom: 3rem;
}

.form {
  background-color: var(--secondary-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 40px;
  border-radius: 5px;
  color: var(--primary-color);
  border-radius: 10px;
  width: 100%;
}

.form--input,
textarea {
  background-color: var(--tertiary-color);
  border: none;
  border-radius: 5px;
  height: 20px;
  color: var(--primary-color);
  font-size: 17px;
  width: 100%;
  padding-left: 2px;
}

.form-button-bundle {
  display: flex;
  gap: 10px;
}

textarea {
  height: 200px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  resize: none;
}

textarea:focus,
input:focus {
  outline: none;
}

.form h1 {
  margin: 0px;
  margin-bottom: 10px;
}

.Form--skillBtn {
  border: none;
  background-color: var(--primary-color);
  border-radius: 5px;
  color: white;
}

.Form--addSKillBtn {
  height: 20px;
  width: 100px;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border: none;
  border-radius: 5px;
  font-size: 17px;
  font-weight: 900;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Form--skillBundle {
  display: flex;
  gap: 10px;
  justify-content: space-between;
  width: 100%;
}

.Form--addSKillBtn:active {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.Form--deleteBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14px;
  height: 14px;
  padding: 5px;
  border-radius: 3px;
  background-color: var(--primary-color);
}

.Form--icon {
  max-width: 20px;
  max-height: 20px;
}

.form--workExperienceBundle input,
.form--workExperienceBundle textarea,
.form--educationBundle input {
  margin-bottom: 10px;
  border-radius: 5px;
  color: var(--primary-color);
}

.form--workExperienceBundle,
.form--educationBundle {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.uploadBtn,
.downloadBtn {
  font-size: 15px;
  color: var(--secondary-color);
  background-color: var(--primary-color);
  border-radius: 3px;
  padding: 8px;
  border: none;
  box-shadow: none;
  display: flex;
  align-items: center;
  gap: 10px;
}

.form--inputBundle {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

/* ==================
    CV component
===================== */

.CV {
  position: fixed;
  height: calc(707px * var(--shrink-factor));
  width: calc(500px * var(--shrink-factor));
  background-color: white;
  top: calc(var(--shrink-factor) * (50% - 353.5px));
  right: calc(var(--shrink-factor) * (27.5% - 230px));
  border-radius: 3px;
  color: black;
  display: grid;
  grid-template-columns: 4fr 6fr;
}

.CV--headline {
  font-size: calc(15px * var(--shrink-factor));
  font-weight: bold;
  letter-spacing: calc(0.4px * var(--shrink-factor));
}

.CV--headline:after {
  content: " ";
  display: block;
  border: calc(2px * var(--shrink-factor)) solid var(--secondary-color);
}
.CV--left,
.CV--right {
  margin: calc(20px * var(--shrink-factor));
  font-size: calc(10px * var(--shrink-factor));
}

.CV--img {
  max-width: calc(140px * var(--shrink-factor));
}

.CV--firstName {
  margin-top: calc(5px * var(--shrink-factor));
  font-size: calc(18px * var(--shrink-factor));
  font-weight: 800;
}

.CV--lastName {
  font-weight: 800;
  font-size: calc(21px * var(--shrink-factor));
  color: var(--secondary-color);
}

.CV--title {
  margin-top: calc(8px * var(--shrink-factor));
  font-size: calc(13px * var(--shrink-factor));
  margin-bottom: calc(20px * var(--shrink-factor));
}

.CV--icon {
  max-width: calc(17px * var(--shrink-factor));
  margin-right: calc(6px * var(--shrink-factor));
}

ul {
  list-style: none
}
ul li::before {
  content: "\200B";
  color: green;
}

li {
  margin-bottom: calc(2px * var(--shrink-factor));
}

ul li::before {
  content: "\2022";
  color: #cedebd;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

.CV--aboutMe {
  font-size: calc(11px * var(--shrink-factor));
  margin-top: calc(10px * var(--shrink-factor));
  margin-bottom: calc(10px * var(--shrink-factor));
  text-align: left;
}

.bundle {
  margin-top: calc(9px * var(--shrink-factor));
  margin-bottom: calc(5px * var(--shrink-factor));
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.CV ul {
  font-size: calc(14px * var(--shrink-factor));
  padding-left: calc(15px * var(--shrink-factor));
}

.CV--arrayBundle {
  display: flex;
  flex-direction: column;
  gap: calc(1px * var(--shrink-factor));
  margin-bottom: calc(14px * var(--shrink-factor));
  margin-top: calc(5px * var(--shrink-factor));
}

.CV--timeframe {
  font-size: calc(13px * var(--shrink-factor));
}

.CV--position {
  margin-top: calc(1px * var(--shrink-factor));
  font-size: calc(15px * var(--shrink-factor));
  margin-bottom: calc(1px * var(--shrink-factor));
  font-weight: bold;
}

.CV--institution {
  font-size: calc(13px * var(--shrink-factor));
  margin-bottom: calc(5px * var(--shrink-factor));
}

.CV--description {
  font-size: calc(11px * var(--shrink-factor));
}

/* ==================
    Media Queries
===================== */

@media only screen and (max-width: 1000px) {
  .layout {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin: 0 auto;
  }

  .CV {
    position: inherit;
    margin: 40px;
  }

  .form--layout {
    margin-left: 0;
    margin-bottom: 0;
    width: 90%;
    align-items: center;
    align-self: center;
  }
}

@media only screen and (max-width: 550px) {
  :root {
    --shrink-factor: 0.8;
  }
}

@media only screen and (max-width: 450px) {
  :root {
    --shrink-factor: 0.65;
  }
}

@media only screen and (max-width: 360px) {
  :root {
    --shrink-factor: 0.5;
  }
}

/* ==================
    CVPrint component
===================== */

.CVPrint {
  position: fixed;
  height: calc(707px * 1.5);
  width: calc(500px * 1.5);
  background-color: white;
  top: calc(1.5 * (50% - 353.5px));
  right: 20000px;
  border-radius: 3px;
  color: black;
  display: grid;
  grid-template-columns: 4fr 6fr;
}

.CVPrint--headline {
